.Permission {
  width: max(40%, 50vh);
  z-index: 1;
}

.Permission h2 {
  margin-bottom: calc(var(--font-size) * 3);
  color: #51D8FF;
  font-size: calc(var(--font-size) * 2);
}

.Permission p {
  margin-bottom: calc(var(--font-size) * 5);
  line-height: 1.5;
}

.Permission div {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.Permission button {
  padding: calc(var(--font-size) * 0.75);
  width: 25vh;
  font-weight: 700;
  color: #0A0A0A;
  border-radius: calc(var(--font-size) * 1.5);
  box-shadow: 0 0 calc(var(--font-size) * 0.25) #0A0A0A;
  transition: all .25s ease-out;
}

.Permission button:first-of-type { background-color: #51D8FF; }

.Permission button:hover,
.Permission button:focus {
  color: #808080;
  box-shadow: 0 0 calc(var(--font-size) * 0.25) inset #0A0A0A;
}

@media only screen and (orientation: portrait), (max-aspect-ratio: 3/2) {
  .Permission { text-align: center; }

  .Permission div { flex-direction: column; }

  .Permission button { width: 100%; }

  .Permission button:first-of-type { margin-bottom: calc(var(--font-size) * 0.25); }
}