@keyframes glowing {
  from {
    text-shadow:
      0 0 calc(var(--font-size) * 0.125) #51D8FF,
      0 0 calc(var(--font-size) * 0.250) #46C7FF,
      0 0 calc(var(--font-size) * 0.375) #3DB5FF,
      0 0 calc(var(--font-size) * 0.500) #239BFE,
      0 0 calc(var(--font-size) * 0.625) #1672F9,
      0 0 calc(var(--font-size) * 0.750) #044EE3,
      0 0 calc(var(--font-size) * 0.875) #23278D,
      0 0 calc(var(--font-size) * 1.000) #121C7B,
      0 0 calc(var(--font-size) * 1.125) #0A185F,
      0 0 calc(var(--font-size) * 1.250) #011140,
      0 0 calc(var(--font-size) * 1.375) #000C24;
  }
  to {
    text-shadow:
      0 0 calc(var(--font-size) * 0.250) #51D8FF,
      0 0 calc(var(--font-size) * 0.375) #46C7FF,
      0 0 calc(var(--font-size) * 0.500) #3DB5FF,
      0 0 calc(var(--font-size) * 0.625) #239BFE,
      0 0 calc(var(--font-size) * 0.750) #1672F9,
      0 0 calc(var(--font-size) * 0.875) #044EE3,
      0 0 calc(var(--font-size) * 1.000) #23278D,
      0 0 calc(var(--font-size) * 1.125) #121C7B,
      0 0 calc(var(--font-size) * 1.250) #0A185F,
      0 0 calc(var(--font-size) * 1.375) #011140,
      0 0 calc(var(--font-size) * 1.500) #000C24;
  }
}

@keyframes spin {
  0%, 25% { transform: rotate(0); }
  100% { transform: rotate(360deg); }
}

@keyframes dim { 25% { filter: drop-shadow(0 0 var(--font-size) #51D8FF); } }