.Loading {
  display: flex;
  justify-content: center;
  height: calc(var(--font-size) * 7.5);
  width: calc(var(--font-size) * 7.5);
  position: relative;
  z-index: 1;
}

.Loading > div {
  position: absolute;
  bottom: 50%;
  overflow: hidden;
  transform-origin: 50% 100%;
  animation: spin 2s cubic-bezier(.175, .885, .32, 1.275) alternate infinite;
}

.Loading > div:first-child {
  height: 50%;
  width: 100%;
  animation-delay: -0.5s;
}

.Loading > div:nth-child(2) {
  height: 45%;
  width: 90%;
  animation-delay: -0.45s;
}

.Loading > div:nth-child(3) {
  height: 40%;
  width: 80%;
  animation-delay: -0.4s;
}

.Loading > div:nth-child(4) {
  height: 35%;
  width: 70%;
  animation-delay: -0.35s;
}

.Loading > div:nth-child(5) {
  height: 30%;
  width: 60%;
  animation-delay: -0.3s;
}

.Loading > div:nth-child(6) {
  height: 25%;
  width: 50%;
  animation-delay: -0.25s;
}

.Loading > div:nth-child(7) {
  height: 20%;
  width: 40%;
  animation-delay: -0.2s;
}

.Loading > div:nth-child(8) {
  height: 15%;
  width: 30%;
  animation-delay: -0.15s;
}

.Loading > div:nth-child(9) {
  height: 10%;
  width: 20%;
  animation-delay: -0.1s;
}

.Loading > div:last-child {
  height: 5%;
  width: 10%;
}

.Loading span {
  display: block;
  height: 200%;
  width: 100%;
  border: solid calc(var(--font-size) * 0.25);
  border-radius: 50%;
  box-shadow: 0 0 var(--font-size) inset #000C24;
}

.Loading div:first-child span { border-color: #51D8FF; }

.Loading div:nth-child(2) span { border-color: #46C7FF; }

.Loading div:nth-child(3) span { border-color: #3DB5FF; }

.Loading div:nth-child(4) span { border-color: #239BFE; }

.Loading div:nth-child(5) span { border-color: #1672F9; }

.Loading div:nth-child(6) span { border-color: #044EE3; }

.Loading div:nth-child(7) span { border-color: #23278D; }

.Loading div:nth-child(8) span { border-color: #121C7B; }

.Loading div:nth-child(9) span { border-color: #0A185F; }

.Loading div:last-child span { border-color: #011140; }