.Main {
  display: flex;
  flex-flow: column wrap;
  align-self: flex-start;
  padding: 5%;
  height: 90%;
  width: 100%;
  z-index: 1;
}

.Location {
  height: 50%;
  width: 50%;
}

.Location span {
  display: inline-block;
  margin: 0 calc(var(--font-size) * 0.25) calc(var(--font-size) * 0.25);
  height: calc(var(--font-size) * 0.25);
  width: calc(var(--font-size) * 0.25);
  background-color: #F5F5F5;
  border-radius: 50%;
}

.Location > p:first-of-type {
  margin-bottom: calc(var(--font-size) * 0.5);
  font-size: inherit;
  font-weight: 700;
}

.Location p:nth-child(2) { margin-bottom: calc(var(--font-size) * 0.5); }

.Location div {
  display: flex;
  height: calc(var(--font-size) * 7.5);
  font-weight: 700;
}

.Location > div p {
  display: flex;
  align-items: flex-start;
  font-size: calc(var(--font-size) * 7.5);
}

.Location div p sup { font-size: calc(var(--font-size) * 2.5); }

.Location div div {
  display: flex;
  flex-direction: column;
  padding-top: calc(var(--font-size) * 0.3);
  height: 80%;
}

.Location button {
  font-size: calc(var(--font-size) * 3.15);
  font-weight: inherit;
  background: none;
}

.Icon {
  height: 100%;
  width: 50%;
  order: 3;
  position: relative;
  text-align: right;
}

.Icon h1 {
  margin-bottom: calc(var(--font-size) * 0.5);
  font-size: calc(var(--font-size) * 2);
}

.Icon img {
  max-height: 100%;
  max-width: 100%;
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -1;
  object-fit: contain;
  filter: drop-shadow(0 0 var(--font-size) #044EE3);
  animation: dim 2s cubic-bezier(.175, .885, .32, 1.275) infinite;
}

.Info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 50%;
  width: 50%;
}

.Info p {
  margin-bottom: var(--font-size);
  font-weight: 700;
  line-height: 1.5;
}

.Info ul {
  display: flex;
  justify-content: space-between;
}

.Info li {
  list-style-type: none;
  line-height: 1.5;
}

.Info span {
  display: block;
  font-weight: 700;
}

@media only screen and (orientation: portrait), (max-aspect-ratio: 3/2) {
  .Main { flex-flow: row wrap; }

  .Icon {
    height: 50%;
    order: 2;
  }

  .Icon img { bottom: -25%; }

  .Info {
    width: 100%;
    order: 3;
  }
}

@media only screen and (orientation: landscape) and (max-height: 600px) { .Info { justify-content: flex-end; } }