@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  line-height: 1;
}

body {
  --font-size: 2.5vh;
  font-family: 'Rajdhani', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: var(--font-size);
  color: #F5F5F5;
  background-color: #0A0A0A;
  user-select: none;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
}

a,
button {
  color: inherit;
  font: inherit;
  border: none;
  cursor: pointer;
}

a:focus,
button:focus { outline: none; }

@media only screen and (orientation: landscape) and (max-height: 600px) { body { --font-size: 1.5vw; } }