.Error {
  width: max(40%, 50vh);
  z-index: 1;
}

.Error button {
  padding: calc(var(--font-size) * 0.75);
  width: 25vh;
  font-weight: 700;
  color: #0A0A0A;
  border-radius: calc(var(--font-size) * 1.5);
  box-shadow: 0 0 calc(var(--font-size) * 0.25) #0A0A0A;
  transition: all .25s ease-out;
}

.Error button:hover,
.Error button:focus {
  color: #808080;
  box-shadow: 0 0 calc(var(--font-size) * 0.25) inset #0A0A0A;
}

.Error h2 {
  margin-bottom: calc(var(--font-size) * 3);
  font-size: calc(var(--font-size) * 5);
  animation: glowing 1s ease-out infinite alternate;
}

.Error p {
  margin-bottom: calc(var(--font-size) * 3.5);
  line-height: 1.5;
}

.Error button { background-color: #044EE3; }

@media only screen and (orientation: portrait), (max-aspect-ratio: 3/2) {
  .Error { text-align: center; }

  .Error button { width: 100%; }

  .Error h2 { margin-bottom: calc(var(--font-size) * 3); }

  .Error p { margin-bottom: calc(var(--font-size) * 7.75); }
}