.Background {
  height: 100%;
  width: 100%;
  position: absolute;
  transition: all .5s ease-out;
}

.Background img {
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  opacity: 0.5;
  transition: all .5s ease-out;
}

.Background div {
  position: absolute;
  left: 5%;
  bottom: 5%;
  font-size: calc(var(--font-size) * 0.75);
  opacity: 0.5;
  z-index: 2;
}

.Background a {
  display: block;
  font-size: calc(var(--font-size) * 0.75);
  line-height: 1.5;
}

@media only screen and (orientation: portrait), (max-aspect-ratio: 3/2) {
  .Background div {
    width: 100%;
    left: 0;
    text-align: center;
  }
}